body {
  background-color: #282c34;
  font-family: "Work Sans", serif;
}

.app_mobile_background {
  background: #282c34;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
  margin: 0 0;
  font-family: "Work Sans", serif;
  @media (width <= 1024px) {
    background-color: white;
  }
}

.app_mobile_main_container {
  background-color: white;
  width: 393px;
  height: 812px;
  margin: 0 auto;
  text-align: center;
  color: rgb(13, 13, 13);
}
@media (width <= 1024px) {
}
.isMobile {
  width: 393px;
  height: 800px;
}

.isTablet {
  width: 1024px;
  height: 1366px;
}

.isDesktop {
}

.input-box-primary {
  border-radius: 6px;
  padding: 10px 10px;
  border: 1px solid #c9c9c9;
  margin: 5px 0;
}

.input-box-primary::placeholder {
  padding: 0 5px;
  font-size: 12px;
}

.signup-button-container {
  margin-top: 80px;
}

.socials-button-container {
  width: 330px;
  margin: 0 auto;
  text-decoration: none;
}

