.socials-button {
  width: 100%;
  border: 1px solid #c9c9c9;
  border-radius: 12px;
  padding: 6px 0;
  background-color: white;
  margin-top: 10px;
}

#apple-button {
  padding: 10px 0 !important;
  .socials-button-label-text {
    margin-left: 10px;
  }
}
.socials-button-content-container {
  align-items: center;
  display: flex;
  justify-content: center;
}

.socials-button-label-text {
  margin-left: 5px;
}

